.header {
	margin-bottom: 55px;
	.container {
		padding: 0; } }

.header-nav {
	display: flex;
	align-items: center;
	&__item {
		font-size: .875rem;
		line-height: 1.063rem;
		margin-right: 45px;
		&:last-child {
			margin-right: 0; } } }


.header-logo {
	&__img {
		max-width: 100%;
		height: auto; } }

.header-phone {
	margin-right: 30px;

	font-size: 1rem;
	line-height: 120%;
	font-weight: bold;
	color: var(--black); }

.header-delivery {
	display: flex;
	&__item {
		display: inline-flex;
		align-items: center;
		margin-right: 20px;
		&__icon {
			margin-right: 6px; }
		&__text {
			font-size: 0.813rem;
			font-weight: bold;
			line-height: 1.063rem;
			color: var(--white); } } }

.header-search {
	max-width: 355px;
	width: 100%;
	margin-right: 45px;
	&__input {
		padding-right: 35px; }
	&__button {
		background-color: var(--white);
		box-shadow: none;
		cursor: pointer; }
	&__icon {
		margin-left: -25px; } }

.header-cart {
	display: flex;
	&__counter {
		position: relative;
		margin-right: 15px;
		display: flex; }
	&__icon {}
	&__number {
		width: 24px;
		height: 24px;
		display: flex;
		align-items: center;
		justify-content: center;

		border-radius: 100%;
		background-color: var(--white);

		font-weight: bold;
		font-size: .75rem;
		line-height: 120%;
		text-align: center;
		color: var(--vinishkotyan);

		margin-top: -9px;
		margin-left: -2px; }

	&__price {
		font-size: 18px;
		font-weight: bold;
		line-height: 120%;
		color: var(--white); } }



.header-top {
	background-color: #FAFBFD;
	height: 50px;
	display: flex;
	align-items: center;

	.header-nav {
		&__item {
			color: var(--black); } } }

.header-middle {
	padding-top: 17px;
	padding-bottom: 21px;
	.header-nav {
		justify-content: space-between;
		width: 100%;
		&__item {
			margin-right: 10px;
			font-weight: bold;
			color: var(--vinishkotyan);
			&:last-child {
				margin-right: 0; } } } }

.header-main {
	position: relative;
	background: var(--vinishkotyan);
	max-width: 1200px;
	margin: 0 auto;
	padding: 10px 30px;

	display: flex;
	align-items: center;
	.header-nav {
		margin-right: 50px;
		&__item {
			margin-right: 35px;
			.icon-menu {
				margin-left: 10px; } }
		&__link {
			font-size: 18px;
			font-weight: bold;
			line-height: 120%;
			color: var(--white); } }
